* {
  box-sizing: border-box;
}

body {
  background-color: #f1c40f;
  color: rgb(4, 28, 59);
  font-family: Arial, Helvetica, sans-serif;
}

ul,
ol {
  padding: 0;
  margin: 0;

  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

